<template>
  <div id="container" v-loading="isChartLoading">
    <div id="filter-header">
      <div id="date-picker">
        <el-date-picker
          v-model="timeRange"
          clearable
          type="datetimerange"
          size="default"
          range-separator="—"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          value-format="x"
          @change="handleChangeTimeRange"
        />
      </div>
      <div id="count-select">
        <el-select
          v-model="dataCount"
          placeholder="显示数据条数"
          style="width: 115px"
          clearable
          @change="handleChangeDataCount"
        >
          <el-option label="10" :value="10" />
          <el-option label="30" :value="30" />
          <el-option label="50" :value="50" />
        </el-select>
      </div>
    </div>
    <div id="device-line-chart-box" ref="chart" />
  </div>
</template>

<script setup>
import { ref, onMounted, reactive, defineProps, watch } from 'vue'
import echarts from '@/utils/echarts'
import { useRouterStore } from '@/store/router'
import { fetchStreamData, fetchNewData } from '@/apis/dataStream'
import { formatDate } from '@/utils/formatDate'

const timeRange = ref([])

const props = defineProps({
  extend: {
    type: Boolean,
    default: true
  },
  dataStreamInfo: {
    type: Object,
    default() {
      return {}
    }
  }
})
const isChartLoading = ref(false)

watch(
  () => props.extend,
  () => {
    if (props.extend === true) {
      isChartLoading.value = true
    }
  }
)

const routerStore = useRouterStore()

const dataStreamInfo = reactive({
  productId: routerStore.productId,
  dataTemplateId: props.dataStreamInfo.dataTemplateId,
  dataName: props.dataStreamInfo.dataName,
  unitSymbols: props.dataStreamInfo.unitSymbols,
  unitName: props.dataStreamInfo.unitName
})

const chart = ref(null)
let myChart
onMounted(() => {
  // getStreamInfo()
  myChart = echarts.init(chart.value)
  initChart(myChart)
})
function getUploadData(chart, { end = new Date(), start = new Date(new Date() - 1000 * 3600 * 24 * 7) }) {
  isChartLoading.value = true
  const newOption = {
    xAxis: {
      data: []
    },
    series: [
      {
        // name: '',
        data: []
      }
    ]
  }
  fetchStreamData({
    dataTemplateId: dataStreamInfo.dataTemplateId,
    deviceId: routerStore.deviceId,
    start,
    end
  }).then(res => {
    if (res.code === 200) {
      newOption.xAxis.data = res.data.map(item => formatDate(item.date))
      newOption.series[0].data = res.data.map(item => item.data)
      chart.setOption(newOption)
    }
  }).finally(() => {
    isChartLoading.value = false
  })
}
function initChart(chart) {
  const option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross'
      },
      valueFormatter: (value) => value + dataStreamInfo.unitSymbols
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      name: '时间',
      data: [],
      nameTextStyle: {
        fontStyle: 'italic',
        fontWeight: 'bold',
        fontSize: 15
      },
      axisLine: {
        symbol: ['none', 'arrow']
      }
    },
    yAxis: {
      type: 'value',
      name: dataStreamInfo.dataName + `(${dataStreamInfo.unitSymbols})`,
      nameTextStyle: {
        fontStyle: 'italic',
        fontWeight: 'bold',
        fontSize: 15
      },
      axisLine: {
        show: true,
        symbol: ['none', 'arrow']
      }
    },
    series: [
      {
        data: [],
        name: dataStreamInfo.dataName,
        type: 'line',
        areaStyle: {
          color: {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [{
              offset: 0, color: '#72c1e3' // 0% 处的颜色
            }, {
              offset: 1, color: 'white' // 100% 处的颜色
            }]
          }
        },
        symbolSize: 10,
        lineStyle: {
          width: 6
        }
      }
    ]
  }

  fetchStreamData({
    dataTemplateId: dataStreamInfo.dataTemplateId,
    deviceId: routerStore.deviceId,
    start: new Date(new Date() - 1000 * 3600 * 24 * 7),
    end: new Date()
  }).then(res => {
    if (res.code === 200) {
      option.xAxis.data = res.data.map(item => formatDate(item.date))
      option.series[0].data = res.data.map(item => item.data)
      chart.setOption(option)
    }
  }).finally(() => {
    isChartLoading.value = false
  })
}
function handleChangeTimeRange(value) {
  getUploadData(myChart, { start: new Date(value[0]), end: new Date(value[1]) })
}
const dataCount = ref('')
function handleChangeDataCount(value) {
  if (value !== '') {
    isChartLoading.value = true

    const newOption = {
      xAxis: {
        data: []
      },
      series: [
        {
          data: []
        }
      ]
    }

    fetchNewData({ deviceId: routerStore.deviceId, dataTemplateId: dataStreamInfo.dataTemplateId, count: value }).then(res => {
      if (res.code === 200) {
        newOption.xAxis.data = res.data.map(item => formatDate(item.date))
        newOption.series[0].data = res.data.map(item => item.data)
        myChart.setOption(newOption)
      }
    }).finally(() => {
      isChartLoading.value = false
    })
  }
}
</script>

<style scoped>
#filter-header {
  display: flex;
  flex-direction: row;
}
#date-picker {
  margin-right: 2em;
}
#device-line-chart-box {
  width: 100%;
  height: 300px;
}
</style>
