<template>
  <div id="container-box">
    <div id="header-info" class="context-container">
      <div class="info-item">
        <div class="title-info-item text-info-item">设备数据总数(个)</div>
        <div class="value-info-item text-info-item">{{ dataInfo.deviceDataSum }}</div>
      </div>
      <div class="info-item">
        <div class="title-info-item text-info-item">昨日新增(个)</div>
        <div class="value-info-item text-info-item">{{ dataInfo.yesterdayNew }}</div>
      </div>
      <div class="info-item">
        <div class="title-info-item text-info-item">最近7日新增(个)</div>
        <div class="value-info-item text-info-item">{{ dataInfo.weekNew }}</div>
      </div>
    </div>
    <div id="body-table-data" v-loading="isLoading" class="context-container">
      <el-table :data="streamTableData" style="width: 100%">
        <el-table-column type="expand">
          <template #default="{ row }">
            <el-empty v-if="row.latestData === '暂无数据'" description="暂无数据" />
            <line-chart v-else :data-stream-info="row" />
          </template>
        </el-table-column>
        <el-table-column prop="dataName" label="数据名称" />
        <el-table-column prop="latestData" label="最新数据值" />
        <el-table-column prop="uploadDate" label="最后更新时间" />
      </el-table>
      <div id="pagination">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="5"
          :page-size="5"
          @current-change="(e) => getDataStream(e, 5)"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { deviceDataUploadInfo } from '@/apis/device'
import { fetchDataStream, fetchLatestData } from '@/apis/dataStream'
import { reactive, onMounted, ref } from 'vue'
import { useRouterStore } from '@/store/router'
import { ElMessage } from 'element-plus'
import LineChart from '@/views/device/component/lineChart'
import { formatDate } from '@/utils/formatDate'

onMounted(() => {
  getDeviceDataInfo()
  getDataStream()
})

const dataInfo = reactive({
  deviceDataSum: 0,
  yesterdayNew: 0,
  weekNew: 0
})
const routerStore = useRouterStore()
function getDeviceDataInfo() {
  deviceDataUploadInfo({ productId: routerStore.productId, deviceId: routerStore.deviceId }).then(res => {
    if (res.code === 200) {
      dataInfo.deviceDataSum = res.data.deviceDataSum
      dataInfo.yesterdayNew = res.data.yesterdayNew
      dataInfo.weekNew = res.data.weekNew
    }
  }, rej => {
    console.log(rej)
    ElMessage({
      type: 'error',
      message: '网络错误'
    })
  })
}

const streamTableData = reactive([])
const isLoading = ref(false)
const totalStream = ref(0)
function getDataStream(page = 1, pageSize = 5) {
  isLoading.value = true
  fetchDataStream({ productId: routerStore.productId, page, pageSize }).then(res => {
    streamTableData.splice(0)
    streamTableData.push(...res.data.streamList)
    totalStream.value = res.data.totalStream
    const dataTemplateIdList = streamTableData.map(item => item.dataTemplateId)
    fetchLatestData({ deviceId: routerStore.deviceId, dataTemplateIdList }).then(res => {
      for (const streamTableDataKey in streamTableData) {
        if (!res.data[streamTableDataKey].isNull) {
          streamTableData[streamTableDataKey].latestData = res.data[streamTableDataKey].latestData
          streamTableData[streamTableDataKey].uploadDate = formatDate(res.data[streamTableDataKey].uploadDate)
        } else {
          streamTableData[streamTableDataKey].latestData = '暂无数据'
          streamTableData[streamTableDataKey].uploadDate = '暂无数据'
        }
      }
    })
  }).finally(() => {
    isLoading.value = false
  })
}
</script>

<style scoped>
.context-container {
  background-color: white;
  border-radius: 5px;
  width: 100%;
  margin-top: 1em;
  border: 1px solid #dcdfe6;
  padding: 1em 1em;
}
.info-item {
  margin-right: 2em;
}
.text-info-item {
  text-align: center;
}
.value-info-item {
  font-weight: bold;
  font-size: 1.25em;
  margin-top: 7px;
}
#header-info {
  display: flex;
  flex-direction: row;
}
#pagination {
  display: flex;
  justify-content: right;
  margin-right: 8%;
  padding-bottom: 2em;
}
</style>
